import React from 'react'
import styled from 'styled-components'
import {Navbar, Nav} from 'react-bootstrap'
import AnchorLink from 'react-anchor-link-smooth-scroll'

const NavBar = () => {
    return (
        <NavContainer >
            <Navbar className='bb'expand="lg" >
            <Navbar.Brand href="#home"></Navbar.Brand>
            <Navbar.Toggle className = '' aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mx-auto">
                <AnchorLink className='atag' href="#calendar" >Calendar</AnchorLink>
                <AnchorLink className='atag' href="#tiktok" >Tiktok</AnchorLink>
                <AnchorLink className='atag' href="#memories" >Memories</AnchorLink >
                <AnchorLink className='atag' href="#about" >About</AnchorLink >
                </Nav>
            </Navbar.Collapse>
            </Navbar>
        </NavContainer>
    )
}

export default NavBar

const NavContainer =styled.div`
    .bb{
        background-image: linear-gradient(to left, #74ebd5, #9face6);  
    }
  
    .atag{
        text-decoration:none;
        font-family: 'Lobster', cursive;
        
        font-size:1.2rem;
        padding:1rem;
        color: black;
        padding:1rem;
        margin-right:4rem;
        &:hover{
            color: #F0FFFF;
        }
        
    }

`